	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"First Day of \"Work\"","date":"01 Jul 2007","slug":"/2007-07-01-first-day-of-work/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/682463062/\" title=\"Photo Sharing\"><img src=\"http://farm2.static.flickr.com/1070/682463062_b2525d2d91.jpg\" width=\"500\" height=\"375\" alt=\"IMG_2069\" /></a></p>\n<p>Even though we got home at 3:30 this morning, Christopher insisted on going to the restaurant at 9:00 am for his first day of \"work\". We were actually already awake, I guess we can catch up on sleep later.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/683006302/\" title=\"Photo Sharing\"><img src=\"http://farm2.static.flickr.com/1394/683006302_cd6a535f3a_o.jpg\" width=\"330\" height=\"250\" alt=\"crh_work\" /></a></p>\n<p>Christopher actually kept himself busy for nearly 3 hours. My CF card ate the rest of the photos I took today, but I was able to recover the thumbnails.</p>"},"next":{"fields":{"title":"14 Hours","date":"30 Jun 2007","slug":"/2007-06-30-14-hours/"}},"previous":{"fields":{"title":"Our Name On The Door","date":"01 Jul 2007","slug":"/2007-07-01-our-name-on-the-door/"}}},{"node":{"fields":{"title":"14 Hours","date":"30 Jun 2007","slug":"/2007-06-30-14-hours/","tags":"Y2007"},"html":"<p>Yesterday I thought it was funny that we'd have only one car for 24 hours (Frauke will borrow her mother's car until her Touran arrives). Not anymore. This morning as Frauke wanted to drive to the bakery, the Lupo refused to start. The engine catches, but when we release the key it cuts out after 1 or 2 seconds. </p>\n<p>We're now waiting for the tow truck from our VW garage to arrive and to arrange a loaner car. This certainly puts a kink in our plans for today. :-(</p>\n<p>Update: Just as I was to post this entry, the tow truck arrived with the loaner car on board. Our business has been saved!</p>"},"next":{"fields":{"title":"Before...","date":"29 Jun 2007","slug":"/2007-06-29-before/"}},"previous":{"fields":{"title":"First Day of \"Work\"","date":"01 Jul 2007","slug":"/2007-07-01-first-day-of-work/"}}},{"node":{"fields":{"title":"Before...","date":"29 Jun 2007","slug":"/2007-06-29-before/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/657723026/\" title=\"Photo Sharing\"><img src=\"http://farm2.static.flickr.com/1079/657723026_38c8865b93.jpg\" width=\"500\" height=\"375\" alt=\"IMG_2064\" /></a></p>\n<p>Today is Frauke's last official day as an employee, so she has to drop off her company Audi at the office.</p>"},"next":{"fields":{"title":"McBier","date":"27 Jun 2007","slug":"/2007-06-27-mcbier/"}},"previous":{"fields":{"title":"14 Hours","date":"30 Jun 2007","slug":"/2007-06-30-14-hours/"}}}],"pathPrefix":"","first":false,"last":false,"index":117,"pageCount":1101}}

